export function scan() {
  // 
  return new Promise((resolve, reject) => {
    uni.scanCode({
      onlyFromCamera: true,
      success: function (res) {
        resolve(res);
        console.log("" + res.scanType);
        console.log("" + res.result);
      },
      fail(e) {
        uni.showToast({
			title:"",
			icon:"none"
		});
        reject(e);
      },
    });
  });
}
