import {post} from "./request.js"
// 
export function getOauthName() {
    let name;
    uni.getProvider({
        service: 'oauth',
        success(e) {
            name = e.provider[0];
        }
    });
    return name;
}

// 
export function userLogin(agentId) {
    let name = getOauthName();
    return new Promise((resolve, reject) => {
        uni.login({
            provider: name,
            scopes: 'auth_base',
            success: function(loginRes) {
                uni.getUserInfo({
                    provider: name,
                    success: function(infoRes) {
                        uni.setStorageSync('user_info', infoRes.userInfo);
						let data = Object.assign(loginRes,infoRes)






















						console.log(infoRes)
						post('login/weixin',{
							code: data.code,
							encryptedData: data.encryptedData,
							iv: data.iv,
							agentId: agentId
						}).then(res=>{
							if(res.code === 200) {
								uni.setStorageSync('user_info',data);
								uni.setStorageSync('authCode',res.data)
							} else {
								uni.showToast({
									title: res.msg,
									icon: 'none',
									duration: 3000
								})
							}
							resolve(res.data);
						})

                    },
                    fail(err) {
                        reject(err);
                    }
                });
            },
            fail(err) {
                reject(err);
            }
        });
    })
}

