export function getLocation() {
    return new Promise((resolve, reject) => {
        uni.getLocation({
			type:"gcj02",
            success: function(res) {
                uni.setStorageSync('user_address', res);
				console.log("this.addressInfo1",res)
                resolve(res);
            },
            fail(e) {
                uni.showToast({
					title:''
				});
                reject(err);
            }
        });
    })
}

// 
export function getSetting() {
    return new Promise((resolve, reject) => {
        uni.getSetting({
            success: function(res) {
                let data = res.authSetting;
            },
            fail(e) {
                uni.showToast('');
                reject(err);
            }
        });
    })
}